<template>
  <div class="container-fluid">
    <div class="row mt-3">
      <div class="col-3">
        <div class="card">
          <div class="card-header">
            Tasks
          </div>
          <ul class="list-group list-group-flush">
            <!-- Tasks Headers here -->
            <li class="list-group-item pointer" v-for="task in tasks" :key="task.id" @click="select_task_by_id(task.id)">{{task.id}}</li>
          </ul>
        </div>
      </div>
      <div class="col-9">
        <!-- Tasks cards Here -->
        <div class="container">
          <TaskCard :is="selected_task" :id="selected_task.id" :struct="selected_task.struct" :active="selected_task.active"/>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import TaskCard from './components/TaskCard.vue';
import { io } from "socket.io-client";


export default {
  name: 'App',
  components: {
    TaskCard
  },
  data() {
    return {
      tasks: [{
        "id": "Welcome to VSUI",
        "struct":
          [{ "key": "Welcome to VSUI", "component": "LogElement", "data": ['no data loaded'] }],
        "active": false
      }],
      selected_task_index: 0,
      socket : {}
    };
  },
  computed: {
    selected_task() {
      return this.tasks[this.selected_task_index]
    }
  },
  methods: {
    select_task_by_id(target_id) {
      this.selected_task_index = this.tasks.findIndex(x => x.id === target_id);
    },
    set_tasks(tasks) {
      this.tasks = tasks;
    }
  },
  created() {
    this.socket=io( {
        extraHeaders: {
            'type' : 'web'
        }
    });

/*     this.socket.on('connect', function () {
        console.log('connected')
    });

    this.socket.on('data_push', function (data) {
      console.log('got a data push')
      console.log(data)
      this.set_tasks(data)
      console.log(this.tasks)
    }); */
  }
};


</script>

<style>
li.pointer {
  cursor: pointer
}
</style>
